import clsx from "clsx"
import {useEffect, useState} from "react"
import Ripple from "~/components/UI/Ripple/Ripple"
import "./Switch.css"

interface SwitchProps extends Omit<React.ButtonHTMLAttributes<HTMLButtonElement>, "select" | "onSelect"> {
    selected?: boolean | undefined
    onSelect?: ((event: React.MouseEvent<HTMLButtonElement>) => void) | undefined
}

const Switch: React.FC<SwitchProps> = props => {
    const {className, selected = false, onSelect, children, ...otherProps} = props
    const [_selected, _setSelected] = useState(selected)

    useEffect(() => {
        _setSelected(selected)
    }, [selected])

    const onButtonClickHandler = (event: React.MouseEvent<HTMLButtonElement>) => {
        if (onSelect) onSelect(event)
    }

    return (
        <Ripple>
            <button
                className={clsx("switch-n8h6", _selected ? "selected-n8h6" : "", className)}
                onClick={onButtonClickHandler}
                {...otherProps}
            >
                {children}
            </button>
        </Ripple>
    )
}

export default Switch
