import clsx from "clsx"
import {useState} from "react"
import Drawer from "~/components/UI/Drawer/Drawer"
import ChevronUpIcon from "~/icons/ChevronUpIcon"
import "./Accordion.css"

export interface AccordionProps extends Omit<React.HTMLAttributes<HTMLDivElement>, "title"> {
    title?: string | null | undefined
    startClosed?: boolean | undefined
    titleClassName?: string | undefined
    iconClassName?: string | undefined
}

const Accordion: React.FC<AccordionProps> = props => {
    const {className, titleClassName, iconClassName, title, startClosed = false, children, ...otherProps} = props
    const [closed, setClosed] = useState(startClosed)

    return (
        <div className={clsx("accordion-90d3", closed ? "closed-90d3" : "", className)} {...otherProps}>
            <button className="button-90d3" onClick={e => setClosed(state => !state)}>
                <ChevronUpIcon className={clsx("icon-90d3 ", iconClassName)} />
                <span className={clsx(titleClassName, "")}>{title}</span>
            </button>
            <Drawer closed={closed} className="content-90d3">
                {children}
            </Drawer>
        </div>
    )
}

export default Accordion
