import {graphql, useStaticQuery} from "gatsby"
import {useContext} from "react"
import I18nContext from "~/contexts/I18nContext"

export const useEventPostsQuery = () => {
    const i18n = useContext(I18nContext)

    const data: Queries.EventPostsQuery = useStaticQuery(graphql`
        query EventPosts {
            allContentfulContenutoMagazine(filter: {nascosto: {ne: true}, type: {eq: "events"}}) {
                nodes {
                    date
                    title
                    category
                    type
                    tags
                    description_short
                    slug
                    image {
                        url
                        gatsbyImageData(
                            layout: CONSTRAINED
                            formats: [AUTO, AVIF, WEBP]
                            placeholder: DOMINANT_COLOR
                            quality: 90
                        )
                        title
                    }
                    immagineSocialShare {
                        title
                        height
                        width
                        url
                        gatsbyImageData(
                            layout: FULL_WIDTH
                            formats: [AUTO, AVIF, WEBP]
                            placeholder: BLURRED
                            quality: 90
                        )
                    }
                    node_locale
                }
            }
        }
    `)

    const localizedData = []
    for (const post of data.allContentfulContenutoMagazine.nodes) {
        if (post.node_locale === i18n.language) {
            localizedData.push(post)
        }
    }
    return localizedData
}
