import {useLocation} from "@reach/router"

export const getQueryParam = (paramName: string) => {
    const search = new URLSearchParams(useLocation().search)
    return search.get(paramName)
}

export const setQueryParams = (...urlParams: Array<URLParam | undefined>) => {
    const url = generateQueryParams(...urlParams)
    if (url.length > 0) {
        history.replaceState(null, "", `?${url}`)
    } else {
        history.replaceState(null, "", location.pathname)
    }
}

export const generateQueryParams = (...urlParams: Array<URLParam | undefined>): string => {
    const queryStrings: Array<string> = []
    urlParams.forEach(urlParam => {
        if (!urlParam || !urlParam.name || !urlParam.value) return
        queryStrings.push(`${urlParam.name}=${urlParam.value}`)
    })
    const url = queryStrings.join("&")
    return url
}

export interface URLParam {
    name: string | undefined
    value: string | undefined
}
