import clsx from "clsx"
import {GatsbyImage} from "gatsby-plugin-image"
import React, {useContext} from "react"
import GenericLink from "~/components/UI/GenericLink/GenericLink"
import I18nContext from "~/contexts/I18nContext"
import {useLabelsQuery} from "~/queries/LabelsQuery"
import {PostItem, PostTypes} from "~/types/PostItem"
import LinkButton from "../../UI/Button/LinkButton"

import "./Post.css"

export interface PostProps extends Omit<React.HTMLAttributes<HTMLDivElement>, "title"> {}

type PostStyle = 1 | 2 | 3 | 4

const Post: React.FC<PostProps & PostItem> = props => {
    const {
        date,
        image,
        title,
        category,
        tags,
        description_short,
        pdf,
        slug,
        customer,
        project,
        className,
        children,
        type,
        ...otherProps
    } = props
    const labels = useLabelsQuery()
    const i18n = useContext(I18nContext)

    let formattedDate
    if (date) {
        formattedDate = new Intl.DateTimeFormat(i18n.language, {
            day: "numeric",
            month: "long",
            year: "numeric",
        }).format(new Date(date))
    }

    let postStyle: PostStyle = 1

    switch (type) {
        case PostTypes.blog:
            postStyle = 1
            break
        case PostTypes.caseHistory:
            postStyle = 2
            break
        case PostTypes.academy:
            postStyle = 1
            break
        case PostTypes.events:
            postStyle = 1
            break
        case PostTypes.analysis:
            if (pdf) {
                postStyle = 4
            } else {
                postStyle = 3
            }
            break
        case PostTypes.video:
            if (pdf) {
                postStyle = 4
            } else {
                postStyle = 3
            }
            break
        case PostTypes.onDemand:
            if (pdf) {
                postStyle = 4
            } else {
                postStyle = 3
            }
            break
        case PostTypes.podcast:
            if (pdf) {
                postStyle = 4
            } else {
                postStyle = 3
            }
            break
    }

    return (
        <div className={clsx("post-n3j4", className)} {...otherProps}>
            <GenericLink {...(pdf ? {to: pdf?.url} : {to: `/${i18n.language}/${type}/${slug}/`})} className="left-n3j4">
                {image?.gatsbyImageData && image.title && (
                    <GatsbyImage alt={image.title} image={image?.gatsbyImageData} className="image-n3j4" />
                )}
                {(postStyle === 1 || postStyle === 2) && (
                    <div className="overlay-n3j4">
                        {formattedDate && postStyle === 1 && <span className="date-n3j4">{formattedDate}</span>}
                        {postStyle === 1 && <h5 className="title-n3j4">{title}</h5>}
                        {postStyle !== 1 && <h4 className="title-n3j4">{title}</h4>}
                    </div>
                )}
            </GenericLink>
            <div className="right-n3j4">
                <div className="lip-n3j4" />
                {(postStyle === 3 || postStyle === 4) && <div className="text-n3j4">{title}</div>}
                {postStyle === 1 && (
                    <div className="text-n3j4">
                        {`${labels.category}: `}
                        <span className="green-n3j4">{category}</span>
                    </div>
                )}
                {postStyle === 1 && (
                    <div className="text-n3j4">
                        {`${labels.tag}: `}
                        <span className="green-n3j4">{tags?.join(", ")}</span>
                    </div>
                )}
                {postStyle === 2 && (
                    <div className="text-n3j4">
                        {`${labels.customer}: `}
                        <span className="green-n3j4">{customer}</span>
                    </div>
                )}
                {postStyle === 2 && (
                    <div className="text-n3j4">
                        {`${labels.project}: `}
                        <span className="green-n3j4">{project}</span>
                    </div>
                )}
                <div className="text-n3j4">{description_short}</div>
                <div className="cta-n3j4">
                    {postStyle !== 4 && (
                        <LinkButton to={`/${i18n.language}/${type}/${slug}/`}>{labels.read}</LinkButton>
                    )}
                    {postStyle === 4 && (
                        <LinkButton to={pdf?.url} download>
                            {labels.downloadPdf}
                        </LinkButton>
                    )}
                </div>
            </div>
        </div>
    )
}
export default Post
