import {AllHTMLAttributes, CSSProperties, ElementType, FC, useCallback, useEffect, useRef, useState} from "react"
import {onTrasitionEnd} from "~/utilities/transition"

type DrawerProps = AllHTMLAttributes<HTMLElement> & {
    closed: boolean
    as?: ElementType
}

const defaultStyle: React.CSSProperties = {
    overflow: "hidden",
    transitionDuration: "0.5s",
    transitionTimingFunction: "cubic-bezier(0.645, 0.045, 0.355, 1)",
}

const Drawer: FC<DrawerProps> = props => {
    const {children, closed: _closed, as: Component = "div", style, ...otherProps} = props
    const drawerRef = useRef<HTMLElement>(null)
    const [isClosed, setClosed] = useState(_closed)
    const [transitionStyle, setTransitionStyle] = useState<CSSProperties>(_closed ? {display: "none"} : {})

    const collapse = useCallback(() => {
        if (!drawerRef.current) return
        setTransitionStyle({height: drawerRef.current?.scrollHeight + "px"})
        setTimeout(() => {
            onTrasitionEnd(drawerRef.current, () => {
                setTransitionStyle({display: "none"})
                setClosed(true)
            })
            setTransitionStyle({height: "0px"})
        }, 20)
    }, [])

    const expand = useCallback(() => {
        if (!drawerRef.current) return
        setTransitionStyle({height: "0px"})
        setTimeout(() => {
            onTrasitionEnd(drawerRef.current, () => {
                setTransitionStyle({})
                setClosed(false)
            })
            if (!drawerRef.current) return
            setTransitionStyle({height: drawerRef.current.scrollHeight + "px"})
        }, 20)
    }, [])

    useEffect(() => {
        if (!drawerRef.current) return
        if (_closed && !isClosed) {
            collapse()
        }
        if (!_closed && isClosed) {
            expand()
        }
    }, [_closed, isClosed, expand, collapse])

    return (
        <Component style={{...defaultStyle, ...transitionStyle}} ref={drawerRef} {...otherProps}>
            {children}
        </Component>
    )
}

export default Drawer
