import {HeadFC, PageProps} from "gatsby"
import {IGatsbyImageData} from "gatsby-plugin-image"
import {Fragment, useEffect, useRef, useState} from "react"
import HeroSection from "~/components/Sections/HeroSection/HeroSection"
import Search from "~/components/Sections/Search/Search"
import Post from "~/components/Sections/Shelf/Post"
import Shelf, {ShelfSortCategories} from "~/components/Sections/Shelf/Shelf"
import Button from "~/components/UI/Button/Button"
import Fader from "~/components/UI/Fader/Fader"
import Gap from "~/components/UI/Gap/Gap"
import InputText from "~/components/UI/InputText/InputText"
import MultiStyleText from "~/components/UI/MultiStyleText/MultiStyleText"
import ScrollButton from "~/components/UI/ScrollButton/ScrollButton"
import SectionContainer from "~/components/UI/SectionContainer/SectionContainer"
import Switch from "~/components/UI/Switch/Switch"
import {useAcademyPostsQuery} from "~/queries/AcademyPostsQuery"
import {useBlogPostsQuery} from "~/queries/BlogPostsQuery"
import {useCasePostsQuery} from "~/queries/CasePostsQuery"
import {useEventPostsQuery} from "~/queries/EventPostsQuery"
import {useLinksQuery} from "~/queries/LinksQuery"
import {PostItem, PostTypes} from "~/types/PostItem"
import {getQueryParam, setQueryParams} from "~/utilities/queryParams"
import {SEO} from "../components/UI/SEO/SEO"
import {PageContext} from "../types/PageContext"

type MagazinePageContext = PageContext<Queries.MagazinePageQuery["allContentfulPaginaMagazine"]["edges"][0]["node"]>

interface MagazinePageProps extends PageProps<any, MagazinePageContext> {}

export const MagazineSectionParam = "section"
export const MagazineSearchParam = "search"
export const MagazineSortParam = "sort"

export enum SectionIndex {
    index = 0,
    blog = 1,
    caseHistory = 2,
    academy = 3,
    events = 4,
}

interface SectionContent {
    title: string | undefined | null
    image: IGatsbyImageData | undefined | null
    mobileImage: IGatsbyImageData | undefined | null
    imageAlt: string | undefined | null
    slug: string | undefined | null
}

const MagazinePage: React.FC<MagazinePageProps> = props => {
    const magazinePageData = props.pageContext.data

    const getFilteredItems = () => {
        const posts: Array<PostItem> = []
        allPosts.forEach(post => {
            if (selectedSectionName !== PostTypes.all && post.type !== selectedSectionName) return
            if (inputTextValue !== "") {
                let foundInTitle = false
                let foundInDescription = false
                let foundInCategory = false
                let foundInCustomer = false
                let foundInProject = false
                let foundInTags = false

                const lowCaseValueToFind = inputTextValue.toLowerCase()
                if (post.title && post.title.toLowerCase().indexOf(lowCaseValueToFind) > -1) foundInTitle = true
                if (post.description_short && post.description_short.toLowerCase().indexOf(lowCaseValueToFind) > -1)
                    foundInDescription = true
                if (post.category && post.category.toLowerCase().indexOf(lowCaseValueToFind) > -1)
                    foundInCategory = true
                if (post.customer && post.customer.toLowerCase().indexOf(lowCaseValueToFind) > -1)
                    foundInCustomer = true
                if (post.project && post.project.toLowerCase().indexOf(lowCaseValueToFind) > -1) foundInProject = true
                if (post.tags) {
                    post.tags.forEach(tag => {
                        if (tag && tag.toLowerCase().indexOf(lowCaseValueToFind) > -1) foundInTags = true
                    })
                }

                if (
                    !foundInTitle &&
                    !foundInCategory &&
                    !foundInDescription &&
                    !foundInTags &&
                    !foundInCustomer &&
                    !foundInProject
                )
                    return
            }

            posts.push(post)
        })
        return posts
    }

    const asyncFIlterItems = async () => {
        return getFilteredItems()
    }

    const allPosts: Array<PostItem> = [
        ...useBlogPostsQuery(),
        ...useCasePostsQuery(),
        ...useAcademyPostsQuery(),
        ...useEventPostsQuery(),
    ] as Array<PostItem>

    const sections = getSections(props.pageContext)
    const sectionParam = getQueryParam(MagazineSectionParam)
    const searchParam = getQueryParam(MagazineSearchParam)
    const sortParam = getQueryParam(MagazineSortParam)

    const [selectedSectionIndex, setSelectedSectionIndex] = useState<SectionIndex | number>(
        sectionNameToIndex(sectionParam),
    )
    const [selectedSectionName, setSelectedSectionName] = useState<PostTypes | string>(sectionParam ? sectionParam : "")

    const [selectedSort, setSelectedSort] = useState<ShelfSortCategories | string>(sortParam ? sortParam : "")
    const [inputTextValue, setInputTextValue] = useState(searchParam ? searchParam : "")

    const [shelfItems, setShelfItems] = useState<Array<PostItem>>(getFilteredItems())
    const [isLoading, setLoading] = useState<boolean>(false)
    const searchRef = useRef<HTMLElement | null>(null)

    useEffect(() => {
        if (!sectionParam) return
        setSelectedSectionName(sectionParam)
        setSelectedSectionIndex(sectionNameToIndex(sectionParam))
    }, [sectionParam])

    useEffect(() => {
        updateShelf()
    }, [selectedSectionName, selectedSort])

    const onSwitchClickHandler = (event: React.MouseEvent<HTMLButtonElement>) => {
        const selectedType = Number(event.currentTarget.value)
        setSelectedSectionIndex(selectedType)
        setSelectedSectionName(sectionsIndexToname(selectedType))
    }

    const onInputChangeValue = (event: React.ChangeEvent<HTMLInputElement>) => {
        setInputTextValue(event.currentTarget.value)
    }

    const onSortChange = (value: ShelfSortCategories | string) => {
        setSelectedSort(value)
    }

    const updateShelf = () => {
        updateUrl()
        setLoading(true)
        asyncFIlterItems()
            .then(posts => {
                setShelfItems(posts)
            })
            .then(() => {
                setLoading(false)
            })
    }

    const onFormSubmitHandler = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault()
        updateShelf()
    }

    const updateUrl = () => {
        setQueryParams(
            {name: MagazineSectionParam, value: selectedSectionName},
            {name: MagazineSearchParam, value: inputTextValue},
            {name: MagazineSortParam, value: selectedSort},
        )
    }

    return (
        <Fragment>
            <Fader selected={selectedSectionIndex}>
                {sections.map((section, index) => {
                    return (
                        <Fader.Slide key={index}>
                            <HeroSection
                                image={section?.image}
                                imageAlt={section?.imageAlt}
                                mobileImage={section?.mobileImage}
                            >
                                <MultiStyleText
                                    flex
                                    className1="hero-title-part-1-d2bh"
                                    className2="hero-title-part-2-d2bh"
                                >
                                    {section.title}
                                </MultiStyleText>
                            </HeroSection>
                        </Fader.Slide>
                    )
                })}
            </Fader>
            <SectionContainer tabbed marginPost customTab={<ScrollButton />} ref={searchRef}>
                <Search title={magazinePageData.titolo}>
                    <Gap centered style={{marginTop: "var(--gutter-4)"}}>
                        <Switch
                            key={magazinePageData.allLabel}
                            selected={selectedSectionIndex === SectionIndex.index}
                            onSelect={onSwitchClickHandler}
                            value={SectionIndex.index}
                        >
                            {magazinePageData.allLabel}
                        </Switch>
                        {magazinePageData.sezioni?.map((section, index) => {
                            const fixedIndex = index + 1
                            return (
                                <Switch
                                    key={section?.slug}
                                    selected={selectedSectionIndex === fixedIndex}
                                    onSelect={onSwitchClickHandler}
                                    value={fixedIndex}
                                >
                                    {section?.titolo}
                                </Switch>
                            )
                        })}
                    </Gap>
                    <form onSubmit={onFormSubmitHandler}>
                        <Gap centered>
                            <InputText
                                {...(magazinePageData.searchInputPlaceholder
                                    ? {placeholder: magazinePageData.searchInputPlaceholder}
                                    : {})}
                                onChange={onInputChangeValue}
                                value={inputTextValue}
                                style={{
                                    width: "min(30vw,500px)",
                                    minWidth: "240px",
                                    fontStyle: "italic",
                                }}
                            />
                            <Button color="green" type="submit">
                                {magazinePageData.searchButtonLabel}
                            </Button>
                        </Gap>
                    </form>
                </Search>
            </SectionContainer>
            <SectionContainer>
                <Shelf withShowMore withSorting loading={isLoading} sortyBy={sortParam} onSortSelect={onSortChange}>
                    {shelfItems.map((item, index) => {
                        return (
                            <Post
                                key={index}
                                date={item.date}
                                title={item.title}
                                image={item.image}
                                category={item.category}
                                tags={item.tags}
                                description_short={item.description_short}
                                slug={item.slug}
                                type={item.type}
                                customer={item.customer}
                                project={item.project}
                            />
                        )
                    })}
                </Shelf>
            </SectionContainer>
        </Fragment>
    )
}

export default MagazinePage

const getSections = (magazinePageContext: MagazinePageContext): Array<SectionContent> => {
    const sections: Array<SectionContent> = []
    const index: SectionContent = {
        title: magazinePageContext.data.titolo,
        image: magazinePageContext.data.header?.gatsbyImageData,
        mobileImage: magazinePageContext.data.headerMobile?.gatsbyImageData,
        imageAlt: magazinePageContext.data.headerMobile?.title,
        slug: magazinePageContext.data.slug,
    }
    sections.push(index)
    magazinePageContext.data.sezioni?.forEach(sezione => {
        sections.push({
            title: sezione?.titolo,
            image: sezione?.header?.gatsbyImageData,
            mobileImage: sezione?.headerMobile?.gatsbyImageData,
            imageAlt: sezione?.header?.title,
            slug: sezione?.slug,
        })
    })
    return sections
}

export const sectionNameToIndex = (value: string | undefined | null): number => {
    switch (value) {
        case PostTypes.blog:
            return SectionIndex.blog
        case PostTypes.caseHistory:
            return SectionIndex.caseHistory
        case PostTypes.academy:
            return SectionIndex.academy
        case PostTypes.events:
            return SectionIndex.events
        default:
            return SectionIndex.index
    }
}
export const sectionsIndexToname = (value: number | undefined | null): string => {
    switch (value) {
        case SectionIndex.academy:
            return PostTypes.academy
        case SectionIndex.blog:
            return PostTypes.blog
        case SectionIndex.caseHistory:
            return PostTypes.caseHistory
        case SectionIndex.events:
            return PostTypes.events
        default:
            return PostTypes.all
    }
}

export const Head: HeadFC<any, MagazinePageContext> = props => {
    const magazinePageData = props.pageContext.data

    const links = useLinksQuery()

    return (
        <SEO
            title={magazinePageData.titolo ?? magazinePageData.titolo}
            language={props.pageContext.language}
            image={magazinePageData.header?.url}
            imageAlt={magazinePageData.header?.title}
            siteUrl={`${process.env.SITE_URL}${links.magazine.url}`}
        />
    )
}
