import clsx from "clsx"
import {Children, Fragment, useEffect, useRef, useState} from "react"
import Button from "~/components/UI/Button/Button"
import Select, {Option} from "~/components/UI/Select/Select"
import ChevronDownIcon from "~/icons/ChevronDownIcon"
import {useLabelsQuery} from "~/queries/LabelsQuery"
import {PostItem} from "~/types/PostItem"
import {PostProps} from "./Post"
import "./Shelf.css"

interface ShelfProps extends React.HTMLAttributes<HTMLDivElement> {
    label?: string | undefined | null
    withSorting?: boolean | undefined
    withShowMore?: boolean | undefined
    show?: number | undefined
    loading?: boolean | undefined
    sortyBy?: ShelfSortCategories | string | undefined | null
    onSortSelect?: ((value: ShelfSortCategories | string) => void) | undefined | null
}

const Shelf: React.FC<ShelfProps> = props => {
    const {
        className,
        title,
        loading,
        label,
        withSorting = false,
        withShowMore = false,
        sortyBy: sort,
        onSortSelect,
        show = 5,
        children,
        ...otherProps
    } = props
    const [showing, setShowing] = useState(show)
    const [sortBy, setSortBy] = useState<ShelfSortCategories | string>(sort ? sort : ShelfSortCategories.DateC)
    const [items, setItems] = useState<Array<React.ReactElement<PostProps>>>()
    const [isLoading, setLoading] = useState<boolean>(false)
    const timeoutRef = useRef<NodeJS.Timeout | undefined>()
    const labels = useLabelsQuery()

    const onSelectChangeHandler = (event: React.ChangeEvent<HTMLSelectElement>) => {
        setSortBy(event.currentTarget.value)
        if (onSortSelect) onSortSelect(event.currentTarget.value)
    }

    const updateItems = async () => {
        const itemsToShow: Array<React.ReactElement<PostProps & PostItem>> = Children.toArray(children) as Array<
            React.ReactElement<PostProps>
        >

        switch (sortBy) {
            case ShelfSortCategories.CategoryC:
                itemsToShow.sort((a, b) => {
                    if (!a.props.category) return 1
                    if (!b.props.category) return -1
                    return a.props.category.localeCompare(b.props.category)
                })
                break
            case ShelfSortCategories.CategoryD:
                itemsToShow.sort((a, b) => {
                    if (!a.props.category) return -1
                    if (!b.props.category) return 1
                    return b.props.category.localeCompare(a.props.category)
                })
                break
            case ShelfSortCategories.CustomerC:
                itemsToShow.sort((a, b) => {
                    if (!a.props.customer) return 1
                    if (!b.props.customer) return -1
                    return a.props.customer.localeCompare(b.props.customer)
                })
                break
            case ShelfSortCategories.CustomerD:
                itemsToShow.sort((a, b) => {
                    if (!a.props.customer) return -1
                    if (!b.props.customer) return 1
                    return b.props.customer.localeCompare(a.props.customer)
                })
                break
            case ShelfSortCategories.DateC:
                itemsToShow.sort((a, b) => {
                    if (!a.props.date) return 1
                    if (!b.props.date) return -1
                    if (a.props.date > b.props.date) return -1
                    if (b.props.date > a.props.date) return 1
                    return 0
                })
                break
            case ShelfSortCategories.DateD:
                itemsToShow.sort((a, b) => {
                    if (!a.props.date) return -1
                    if (!b.props.date) return 1
                    if (a.props.date > b.props.date) return 1
                    if (b.props.date > a.props.date) return -1
                    return 0
                })
                break
            case ShelfSortCategories.TitleC:
                itemsToShow.sort((a, b) => {
                    if (!a.props.title) return 1
                    if (!b.props.title) return -1
                    return a.props.title.localeCompare(b.props.title)
                })
                break
            case ShelfSortCategories.TitleD:
                itemsToShow.sort((a, b) => {
                    if (!a.props.title) return -1
                    if (!b.props.title) return 1
                    return b.props.title.localeCompare(a.props.title)
                })
                break
            case ShelfSortCategories.TypeC:
                itemsToShow.sort((a, b) => {
                    if (!a.props.type) return 1
                    if (!b.props.type) return -1
                    return a.props.type.localeCompare(b.props.type)
                })
                break
            case ShelfSortCategories.TypeD:
                itemsToShow.sort((a, b) => {
                    if (!a.props.type) return -1
                    if (!b.props.type) return 1
                    return b.props.type.localeCompare(a.props.type)
                })
                break
        }

        setItems(itemsToShow.slice(0, showing))
    }

    useEffect(() => {
        setShowing(show)
        if (loading || sortBy) {
            setLoading(true)
            updateItems().then(result => (timeoutRef.current = setTimeout(() => setLoading(false), 0)))
        }
        return () => {
            if (timeoutRef.current) clearTimeout(timeoutRef.current)
        }
    }, [loading, sortBy])

    useEffect(() => {
        updateItems()
    }, [showing])

    const onShowMoreClickHandler = (event: React.MouseEvent<HTMLButtonElement>) => {
        setShowing(state => state + show)
    }

    return (
        <div className={clsx("shelf-ym83", className)} {...otherProps}>
            {(withSorting || label) && (
                <header className="header-ym83">
                    <span className="label-ym83">{label}</span>
                    {withSorting && (
                        <span className="sort-ym83">
                            <label className="sort-label-ym83" htmlFor="select-ym83">
                                {labels.sortBy}
                            </label>

                            {labels.ascending &&
                                labels.descending &&
                                labels.category &&
                                labels.customer &&
                                labels.date &&
                                labels.title &&
                                labels.type && (
                                    <Select
                                        id="select-ym83"
                                        className="select-ym83"
                                        value={sortBy}
                                        onChange={onSelectChangeHandler}
                                    >
                                        <Option value={ShelfSortCategories.CategoryC}>
                                            {`${labels.category} ${labels.ascending}`}
                                        </Option>
                                        <Option value={ShelfSortCategories.CategoryD}>
                                            {`${labels.category} ${labels.descending}`}
                                        </Option>
                                        <Option value={ShelfSortCategories.CustomerC}>
                                            {`${labels.customer} ${labels.ascending}`}
                                        </Option>
                                        <Option value={ShelfSortCategories.CustomerD}>
                                            {`${labels.customer} ${labels.descending}`}
                                        </Option>
                                        <Option value={ShelfSortCategories.DateC}>
                                            {`${labels.date} ${labels.ascending}`}
                                        </Option>
                                        <Option value={ShelfSortCategories.DateD}>
                                            {`${labels.date} ${labels.descending}`}
                                        </Option>
                                        <Option value={ShelfSortCategories.TitleC}>
                                            {`${labels.title} ${labels.ascending}`}
                                        </Option>
                                        <Option value={ShelfSortCategories.TitleD}>
                                            {`${labels.title} ${labels.descending}`}
                                        </Option>
                                        <Option value={ShelfSortCategories.TypeC}>
                                            {`${labels.type} ${labels.ascending}`}
                                        </Option>
                                        <Option value={ShelfSortCategories.TypeD}>
                                            {`${labels.type} ${labels.descending}`}
                                        </Option>
                                    </Select>
                                )}
                        </span>
                    )}
                </header>
            )}
            {items && (
                <Fragment>
                    {items.length > 0 && <div className="items-ym83">{items}</div>}
                    {items.length < 1 && (
                        <div className="no-result-ym83">
                            <p>{labels.noMatchingResult}</p>
                        </div>
                    )}
                </Fragment>
            )}

            {withShowMore && showing < Children.count(children) && (
                <footer className="shelf-footer-ym83">
                    <Button
                        className="show-more-button-ym83"
                        onClick={onShowMoreClickHandler}
                        type="button"
                        color="transparent"
                    >
                        <ChevronDownIcon className="show-more-icon-ym83" />
                        <span className="show-more-text-ym83">{labels.showMore}</span>
                    </Button>
                </footer>
            )}
        </div>
    )
}

export default Shelf

export enum ShelfSortCategories {
    CategoryC = "categoryC",
    CategoryD = "categoryD",
    CustomerC = "customerC",
    CustomerD = "customerD",
    DateC = "dateC",
    DateD = "dateD",
    TitleC = "titleC",
    TitleD = "titleD",
    TypeC = "typeC",
    TypeD = "typeD",
}
