import clsx from "clsx"
import {forwardRef, useState} from "react"
import ChevronDownIcon from "~/icons/ChevronDownIcon"
import "./Select.css"

interface SelectProps extends Omit<React.SelectHTMLAttributes<HTMLSelectElement>, "placeholder"> {
    placeholder?: string | undefined
    containerProps?: React.HTMLAttributes<HTMLDivElement> | undefined
    invalid?: boolean
}

const Select: React.FC<SelectProps> = forwardRef<HTMLSelectElement, SelectProps>((props, ref): JSX.Element => {
    const {className, invalid, id, onChange, placeholder = " - ", containerProps, children, ...otherProps} = props
    const [selected, setSelected] = useState("undefined")

    const onSelectChangeHandler = (event: React.ChangeEvent<HTMLSelectElement>) => {
        if (onChange) onChange(event)
        setSelected(event.currentTarget.value)
    }

    return (
        <div
            className={clsx(
                "select-container-fn79",
                selected === "undefined" ? "gray-x8np" : "",
                invalid ? "invalid-fn79" : "",
                className,
            )}
            {...containerProps}
        >
            <select id={id} className="select-fn79" onChange={onSelectChangeHandler} ref={ref} {...otherProps}>
                <option value="undefined" key="-1">
                    {placeholder}
                </option>
                {children}
            </select>
            <div className="icon-container-fn79">
                <ChevronDownIcon className="icon-fn79" />
            </div>
        </div>
    )
})

export default Select

interface OptionProps extends React.OptionHTMLAttributes<HTMLOptionElement> {
    children: string
}

export const Option: React.FC<OptionProps> = props => {
    const {children, value, ...otherProps} = props
    return (
        <option value={value ? value : children} {...otherProps}>
            {children}
        </option>
    )
}
