import {graphql, useStaticQuery} from "gatsby"
import {useContext} from "react"
import I18nContext from "~/contexts/I18nContext"

export const useCasePostsQuery = () => {
    const i18n = useContext(I18nContext)

    const data: Queries.CasePostsQuery = useStaticQuery(graphql`
        query CasePosts {
            allContentfulContenutoCaseHistory {
                nodes {
                    customer
                    slug
                    type
                    image {
                        url
                        gatsbyImageData(
                            layout: CONSTRAINED
                            formats: [AUTO, AVIF, WEBP]
                            placeholder: DOMINANT_COLOR
                            quality: 90
                        )
                        title
                    }
                    immagineSocialShare {
                        title
                        height
                        width
                        url
                        gatsbyImageData(
                            layout: FULL_WIDTH
                            formats: [AUTO, AVIF, WEBP]
                            placeholder: BLURRED
                            quality: 90
                        )
                    }
                    title
                    description_short
                    project
                    node_locale
                }
            }
        }
    `)

    const localizedData = []
    for (const post of data.allContentfulContenutoCaseHistory.nodes) {
        if (post.node_locale === i18n.language) {
            localizedData.push(post)
        }
    }
    return localizedData
}
